<template>
  <div class="works_content_page" id="works_content_page">
    <div class="intro_part" id="intro_part">
      <div class="intro_object" id="intro_object">
        <div class="object_inline">Type:<div
            class="tag"
            id="tag"
            v-for="(tag, index) of works_content.tag_list"
            :key="tag"
          > {{ tag
            }}{{ index === works_content.tag_list.length - 1 ? "" : ", " }}</div></div>
        <div class="object_inline">Client: {{ works_content.client }}</div>
        <div class="object_inline">Year: {{ works_content.year }}</div>
      </div>
      <div class="intro_title" id="intro_title">{{ works_content.title }}</div>
      <div class="intro_content" id="intro_content">{{ works_content.intro }}
      </div>
    </div>

    <div v-if="imgs_layout.length !== 0" class="works_part" id="works_part">
      <div
        class="img_row"
        id="img_row"
        v-for="(row, index) of imgs_layout"
        :key="index">

        <img 
        class="img" 
        id="img"
        v-for="(img, index) of row"
        :key="index"
        :style="{width:img.width+'rem', height:img.height+'rem'}"
        :src="img.src" >
      </div>
    </div>
    <Footer></Footer>
    <Menu></Menu>
    <LangTransfer></LangTransfer>
  </div>
</template>

<style scoped lang="css">
div {
  font-family: MyriadVariableConcept-Roman;
  color: #37383d;
}

::-webkit-scrollbar {
  display: none;
}

div.works_content_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  /* display: flex;
  flex-direction: column; */
  overflow-y: scroll;
}

div.intro_part {
  padding-right: 300rem;
  padding-left: 300rem;
  /* position: relative; */
  margin-top: 200rem;
  display: grid;
  /* grid-template-columns: 400rem 100rem; */
}

div.intro_object {
  grid-row: 1;
  grid-column: 2;
  align-self: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 18px;
}

div.object_inline {
  display: inline-block;
  font-size: 18px;
  letter-spacing: 0.5px;
  line-height: 2;
  display: flex;
  flex-direction: row;
  white-space: pre-line;
}

div.tag {
  display: inline-block;
  white-space: pre-wrap;
}

div.intro_title {
  color: #251714;
  font-family: FuturaLT-ExtraBold;
  grid-row: 2;
  grid-column: 1;
  font-size: 90rem;
  white-space: pre-line;
  width: 700rem;
}

div.intro_content {
  grid-row: 2;
  grid-column: 2;
  font-size: 18px;
  letter-spacing: 0.5px;
  line-height: 2;
  white-space: pre-line;
  text-align: justify;
  text-justify: distribute-all-lines;
}

div.works_part{
  position: relative;
  /* font-size: 18px; */
  padding-left: 300rem;
  padding-right: 300rem;
}

div.img_row{
  margin-top: 30rem;
    display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

}
</style>

<script>
import global from "../../global";
import anime from "../../../../modules/anime";
import Menu from "../../../components/menu/Menu";
import LangTransfer from "../../../components/lang_transfer/LangTransfer"
import Footer from "../../../components/footer/Footer"

//export default
export default {
  name: "Works_Content",

  data() {
    return {
      global: global,
      id: this.$route.params.id,
      works_content: require("../../../../static/works/" +
        this.$route.params.id +
        "/config.js")["works_content_" + global.lang],
      works_imgs: require("../../../../static/works/" +
        this.$route.params.id +
        "/config.js").img_list,
        imgs_layout:[]
    };
  },
  components: {
    Menu,
    Footer,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {},
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    this.calc_imgs()
  },
  destroyed() {},
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
    
    calc_imgs(){
      let that = this
      that.works_imgs.forEach((row)=>{
        let row_img = []
        let total = 0
        for(let i = 0; i < row.length; i++){
          total += row[i].proportion
        }
        let space = 30
        let base_width = (1320 - space * (row.length - 1))/total
        let min_height = Number.POSITIVE_INFINITY
        for(let i = 0; i < row.length; i++){
          let img  = {}
          img.width = base_width * row[i].proportion
          img.src = "/static/works/"+ this.id + "/"+row[i].img
          min_height = Math.min(min_height, img.width / row[i].ratio)
          row_img.push(img)
        }
        row_img.forEach((img)=>{
          img.height = min_height
        })
        that.imgs_layout.push(row_img)
      })
      // console.log(that.imgs_layout)

    }
  },
};
</script>



